@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.notfound {
    text-align: center;
    font-family: 'Roboto', sans-serif; 
    color: #888;
}

.notfound h1 {
    font-size: 20rem;
    font-weight: 100;

}

.notfound span {
    font-size: 4rem;
}

.notfound p {
    font-size: 2rem;
    margin: 1rem 0 5rem 0  ;
}

.notfound a {
    margin-top: 3rem;
    font-size: 1.7rem;
    color: #fff;
    background-color: #1797f2;
    padding: 1.5rem 5rem;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: 0.7s ease;
    opacity: 0.8;
}

.notfound a:hover {
    opacity: 1;
}
.login {
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(to right, #3e9fff , #7bcaff);
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login form {
    background-color: #fff;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 1rem;
    width: 40rem;
    padding: 3rem ;
}

.login form h1 {
    font-size: 2.2rem;
    font-weight: bold;
    padding-bottom: 2rem;
}

.login form input {
    padding: 0.8rem 3rem;
    border: 0.2px solid rgba(0, 0, 0, 0.1);
    margin: 1.5rem 0;
    border-radius: 3rem;
    font-size: 1.5rem;
    height: 4rem;
    width: 100%;
    background-color: white;
}

.login form button {
    margin: 1.5rem 0;
    border-radius: 3rem;
    padding: 0.8rem 3rem;
    font-size: 1.5rem;
    height: 4rem;
    width: 100%;
    border: none;
    background-image: linear-gradient(90deg , #3e9fff 0, #7bcaff 70% );
    cursor: pointer;
    color: #fff;
    font-weight: 700;
    transition: 3s ease-in;
}

.login form button:hover {
    background-image: linear-gradient(90deg , #7bcaff 0, #3e9fff 70% );
}

.login form p {
    text-align: center;
    line-height: 1.2rem;
    font-size: 1.5rem;
    padding: 0.8rem 0;
}

.login form p span{
    color: #3e9fff;
    cursor: pointer;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
    font-family: 'Inter', sans-serif;
}


body {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100vh;
    position: relative;
}

#theme {
    display: none;
}

#theme:checked ~ .App {
    -webkit-filter: invert(1);
            filter: invert(1)
}



